* {
  background-color: black;
}
.modelo1 {
  width: 95%;
  overflow-wrap: break-word;
  margin-top: 70px;
  display: flex;
  flex-direction: column;
}
.modelo1 h1 {
  color: white;
  font-family: montserrat;
  font-weight: bold;
  text-align: center;
  font-size: 23px;
}
.modelo1 h2 {
  color: white;
  background-color: red;
  font-family: montserrat;
  font-weight: bold;
  font-size: 23px;
  text-align: center;
  padding: 0px 5px;
}
.modelo2 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 70px;
  text-align: center;
  width: 95%;
}

.modelo2 span {
  color: white;
  font-size: 15px;
  font-family: montserrat;
  font-weight: bold;
}
.modelo2 svg {
  color: red;
  margin-bottom: -2px;
  position: relative;
  right: 2px;
}

.modelo3 {
  width: 95%;
  margin-top: 30px;
}
.modelo3 video {
  width: 100%;
}

.modelo4 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  text-align: center;
  width: 95%;
  margin-bottom: 50px;
}

.modelo4 span {
  color: white;
  font-size: 15px;
  font-family: montserrat;
  font-weight: bold;
}
.modelo4 svg {
  color: red;
  margin-bottom: -2px;
  position: relative;
  right: 2px;
}

.modelo5 button {
  background-color: red;
  border: 0;
  display: flex;
  color: white;
  font-family: montserrat;
  border-radius: 30px;
  padding: 10px 50px;
  font-size: 15px;
  cursor: pointer;
}
.modelo5 {
  margin-bottom: 70px;
}
.modelo5 a {
  text-decoration: none;
}
@media screen and (min-width: 992px) and (max-width: 4000px) {
  .modelo3 video {
    height: 400px;
  }
}
